// ПОПАПЫ И ФОРМЫ




/* Основной скелет для попап-форм */

.popup {
	position: fixed;
	z-index: 200 !important;
	width: 100%;
	height: 100vh;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	visibility: hidden;
	opacity: 0;
	overflow-y: auto;
	transition: opacity 0.3s ease-in-out, background 0.4s ease-out;
	background:rgba(15, 17, 21, 0.8);
}

.popup__body {
	position: relative;
	min-height: 100%;
	width: 100%;
	display: flex;
	align-items: center;
	opacity: 0;
	justify-content: center;

}

.popup__container {
	position: relative;
	padding: rem(24) rem(16);
	width: 100%;
	border-radius: rem(8);
	background-color: $white;
	perspective-origin: right;

	@include smart {
		padding: rem(32) rem(24);
		margin-left: rem(16);
		margin-right: rem(16);
	}

}

.popup-close {
	cursor: pointer;
	position: absolute;
	top: rem(-16);
	right: rem(-16);
	display: flex;
	align-items: center;
	justify-content: center;
	width: rem(48);
	height: rem(48);
	padding: rem(8);
	border-radius: rem(8);
	background-color: $btn;
	border-radius: 50%;
	transition: background-color 0.2s ease-in-out;

	svg {
		transition: 0.2s;
		width: rem(16);
		height: rem(16);
		fill: $white;
	}

	&:hover {
		background-color: $btn-hvr;

	}
}

/* ---------- */


/* Анимация появления формы, отступы чтобы не скакал контент при открытии */


@keyframes anim-OpenPopup {
	0% {
		opacity: 0;
		transform: translateY(-20px);
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
}

.anim-OpenPopup {
	animation-name: anim-OpenPopup;
	animation-duration: 0.3s;
	animation-delay: 0.1s;
	animation-iteration-count: 1;
	animation-timing-function: ease;
	animation-fill-mode: forwards;
}

.pad-r {
	padding-right: rem(17);
}

.mar-l {
	margin-left: rem(-9);
}

/* ---------- */




/* Форма заявки */

.popup-callback {
	align-items: flex-start;

	@include smart {
		align-items: flex-start;

	}
}

.popup-callback__body {

	.popup__container {
		text-align: center;
		margin-left: rem(8);
		margin-right: rem(8);
		border-radius: em(4) em(20) em(4) em(20);
		
		@include smart {
			width: rem(360);
			margin-left: 0;
			margin-right: 0;
		}
	
	}

}

.popup-title {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(10);

	@include smart {
		margin-bottom: rem(20);
	}

	h4 {	
		@include title-font;
		color: $title;
		margin-bottom: 0;
	}
	
	p {
		color: $text;
		@include adap-font(12, 14);
	}
}

.form-request {
	display: flex;
	flex-direction: column;
	align-items: center;

	.input-box {
		position: relative;
		display: flex;
		height: rem(48);
		margin-bottom: rem(8);

		.just-validate-error-label {
			position: absolute;
			top: rem(2);
			right: rem(22);
			font-size: rem(11);
			color: $error !important;
		}

		.just-validate-error-field {
			border-color: $error;
		}

		@include smart {
			height: auto;
		}

	}

	.input--file {
		margin-top: rem(8);
		margin-bottom: rem(8);
	}
	
	.form-btn {
		margin-top: rem(8);
		width: 100%;
		@include smart {
			font-size: rem(18);
		}
	}

}

/* ---------- */




/* Успешная отправка */

.success-notif {
	visibility: hidden;
	opacity: 0;
	position: fixed;
	z-index: 300;
	top: rem(16);
	left: 50%;
	transform: translateX(-50%);
	width: 100%;
	max-width: rem(260);

	@include smart {
		margin-left: 0;
		margin-right: 0;
	}

}

.success__container {
	position: relative;
	display: flex;
	flex-direction: row;
	align-items: center;
	padding: rem(16);
	border-radius: rem(8);
	perspective-origin: right;
	background-color: $success-bg;
	box-shadow: 0 0 1rem #d1dfc9;
}

.ic-success {
	position: relative;
	width: rem(24);
	height: rem(24);
	margin-right: rem(16);

}

.success-text {
	
	p, span {
		line-height: 120%;
	}

	p {
		@include adap-font(14,16);
		font-weight: 700;

	}

	span {
		@include adap-font(13,14);

	}

}

/* ---------- */




/* Форма с описанием */

.popup-info {

	.popup__container {
		top: rem(4);
		width: 100%;
		max-width: rem(768);
		height: rem(800);

		@include smart {
			margin-top: rem(16);
			margin-bottom: rem(16);
		}
	}

}

