// КНОПКА ВВЕРХ




.btnUp-container {
	position: fixed;
	right: rem(16);
	bottom: rem(16);
	opacity: 0.6;
	transition: opacity 0.2s ease-in-out;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	
	&:hover {
		opacity: 0.8;
	}

	@include smart {
		right: rem(32);
		bottom: rem(32);
	}
}

.btn-up {
	display: flex;
	justify-content: center;
	align-items: center;
	width: rem(32);
	height: rem(32);
	border-radius: 50%;
	cursor: pointer;
	transform: rotate(270deg);
	background-color: $btn;

	svg {
		width: rem(16);
		height: rem(16);
		fill: $white;
	}

	@include smart {
		width: rem(40);
		height: rem(40);
	}

}




// КОНЕЦ КНОПКИ ВВЕРХ ==========