// AGREE CHECKBOX 



/* 1 вид без кнопки, сервый текст, серая ссылка */

.agree-checkbox {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
	margin-top: rem(16);
	
	span, a {
		font-size: rem(11);
	}

	a {
		color: $black;
		cursor: pointer;

		&:hover {
			color: $btn;
		}
	}

}

/* ---------- */




/* 2 вид без кнопки серый текст, цветная ссылка */

.agree-checkbox2 {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
	margin-top: rem(16);
	font-size: rem(10);
	user-select: none;

	a {
		cursor: pointer;
	}
}

/* ---------- */




/* 3 вид с кнопкой, серый текст, цветная ссылка */

.agree-checkbox3 {
	position: relative;
	display: flex;
	margin-top: rem(16);
	justify-content: center;
	width: 100%;
	user-select: none;
	text-align: left;
	
	.checkbox-container {
	
		--w-checkmark: 14px;
		--h-checkmark: var(--w-checkmark);
		display: block;
		position: relative;
		padding-left: calc(var(--w-checkmark) + 10px);
		font-size: rem(16);
		cursor: pointer;
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
		
		input {
			position: absolute;
			opacity: 0;
			cursor: pointer;
			height: 0;
			width: 0;
			
		}
	
		.checkmark {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
			width:var(--w-checkmark);
			height: var(--h-checkmark);
			border-radius: rem(2);
			padding: rem(3);
			background-color: $border-input;
			-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
		}
	
		input:checked ~ .checkmark {
			background-color: $btn;
			display: flex;
			justify-content: center;
			align-items: center;

			&:after {
				content: "";
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				left: 2px;
				width: rem(9);
				height: rem(9);
				background-image: url('../img/icons/interface/ic-checkWhite.svg');
				background-repeat: no-repeat;
			}
		}
	
	}

	.checkbox-text {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
	
		span, a {
			font-size: rem(11);
			line-height: 130%;
		}
	
		a {
			cursor: pointer;
			line-height: 120%;
		}
	
	}

	.just-validate-error-label {
		position: absolute;
		bottom: -16px;
		font-size: rem(11);
		color: $error !important;
	}

}

