// БЛОК «HOW IT WORKS»

/* Основа */

.b3-tr {
	position: relative;
	background-color: $bg-light;

	&::after {
		display: none;
		position: absolute;
		top: 0;
		left: 0;
		content: "";

		@include tabletb {
			display: initial;
			width: 100%;
			height: rem(93);
			background-image: url("../img/pic/howWorks/hw-bg.svg");
			background-repeat: no-repeat;
			background-size: cover;
			background-position: center;
		}
	}
}

.b3-c {
	position: relative;
	z-index: 2;
}

.b3pdtb {
	@include tabletb {
		padding-top: rem(120) !important;
	}
}

/* ---------- */

/* Шаги */

.steps {
	display: flex;
	flex-direction: column;

	@include tabletm {
		flex-direction: row;
	}

	& > * {
		margin-bottom: rem(8);
		@include tabletm {
			margin-bottom: 0;
			margin-right: rem(8);
			&:last-child {
				margin-right: 0;
			}
		}
	}
}

.step {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	background-color: $white;
	padding: 0 rem(16) rem(32) rem(16);
	border-radius: rem(56) rem(56) rem(32) rem(32);
	cursor: pointer;
	transition: background-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;

	&:hover {
		background-color: $white;
		border-color: $white;
		@include shd-h;

		.step__btn {
			color: $btn-hvr;
			.ic {
				svg {
					fill: $btn-hvr;
				}
			}
		}
	}

	@include tabletm {
		width: 33%;
	}

	@include tabletb {
		border-radius: rem(88) rem(88) rem(42) rem(42);
		padding: 0 rem(32) rem(32) rem(32);
	}
}

.step__ic {
	position: relative;
	display: flex;
	justify-content: center;
	height: rem(112);

	.ic-wrap {
		position: absolute;
		top: rem(-24);
		left: 50%;
		transform: translateX(-50%);
		width: rem(120);
		height: rem(120);
		border-radius: 50%;
		border: rem(8) solid $bg-light;
	}
}

.step__desc {
	display: flex;
	flex-direction: column;
	align-items: center;

	& > * {
		text-align: center;
	}
}

.step__title {
	margin-bottom: rem(8);
	h4 {
		color: $main-color;
	}
}

.step__text {
	margin-bottom: rem(8);

	@include text-short(2);

	p {
		@include adap-font(14, 18);
	}
}

.step__btn {
	display: flex;
	flex-direction: row;
	align-items: center;
	font-weight: 500;
	color: $title;

	.ic {
		display: block;
		width: rem(20);
		margin-left: rem(10);

		svg {
			transition: fill 0.2s ease-in-out;
			fill: $title;
		}
	}

	&::before {
		display: none;
	}
}

