// БЛОК «ОТЗЫВЫ»

/* Основа */

/* ---------- */

/* слева */

.reviews__pic {
	display: none;
	position: relative;
	@include col-12;

	.pic-w {
		position: absolute;
		left: 0;
		bottom: rem(64);
	}

	@include tabletb {
		display: flex;
		@include col-5;
	}
}

.reviews__pic-bg {
}

/* ---------- */

/* справа */

.reviews__block {
	@include col-12;

	.main-title {
		margin-bottom: rem(40);
	}

	@include tabletb {
		@include col-7;
	}
}

.review {
	padding: rem(12) rem(12) rem(24) rem(12);
	border: 1px solid $border-input;
	border-radius: rem(10);

	@include smart {
		padding: rem(40);
	}
}

.review__time {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	margin-bottom: rem(16);
}

.review-verif {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: flex-start;
	background-color: $success-bg;
	width: rem(150);
	padding: rem(4) rem(10);

	.ic {
		width: rem(12);
		margin-right: rem(8);

		svg {
			fill: $success;
		}
	}

	p {
		font-size: rem(12);
		color: $success;
		font-weight: 600;
	}
}

.review-date {
	p {
		font-size: rem(12);
		color: $text-light2;
	}
}

.review__text {
	margin-bottom: rem(16);
	@include text-short(4);

	p {
		@include adap-font(14, 20);
		font-weight: 500;
	}
}

.reviews-arr-btns {
	margin-top: rem(16);
}

/* ---------- */

// КОНЕЦ БЛОКА «ОТЗЫВЫ» ==========
