h1 {
	@include adap-font(26, 70);
}

h2 {
	@include adap-font(22, 50);
}

h3 {
	@include adap-font(20, 36);
}

h4 {
	@include adap-font(16, 24);
}

h5 {
	@include adap-font(14, 20);
}

h6 {
	@include adap-font(13, 18);

}


h1, h2, h3, h4, h5, h6 {
	@include title-font;
	color: $title;
	line-height: 120%;
	margin-bottom: 0;
}

h1, h2, h3, h4, h5, h6, p, span {
	margin-top: 0;
}

p, span {
	color: $text;
	line-height: 140%;
	margin-bottom: 0;
}

p, a, span {
	@include text-font;
}

a {
	position: relative;
	text-decoration: none;
	background-color: transparent;
	transition: color 0.2s ease-in-out;
	color: $link;

	&::before {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		width: 0;
		height: 1px;
		opacity: 0.5;
		background-color: lighten($link, 20%);
		transition: width 0.2s ease-in-out, background 0.2s ease-in-out;
	}

	&:hover {
		color: $link-clr;

		&::before {
			width: 100%;
			background-color: $btn-hvr;
		}
	}

}

*:focus:not(.focus-visible) {
	outline: none;
}
  
.focus-visible {
	outline-color: lightgreen;
}

