// НАЗВАНИЕ БЛОКА

/* Основа */

.b2-row {
	justify-content: center;
}

.main-title {
	margin-bottom: rem(32);

	h2 {
		span {
			position: relative;
			font-weight: inherit;
			color: inherit;

			&::after {
				position: absolute;
				left: 0;
				bottom: rem(-14);
				content: "";
				width: 100%;
				height: 40px;
				background-image: url("../img/pic/titleLine.svg");
				background-repeat: no-repeat;
				background-size: contain;
				background-position: center;
			}
		}
	}

	@include tabletm {
		margin-bottom: rem(60);
	}
}

/* ---------- */

/* Блюда */

.popular__dishes {
	display: grid;
	grid-template-columns: repeat(1, 1fr);
	gap: rem(10);
	margin-bottom: rem(32);

	@include tabletm {
		grid-template-columns: repeat(2, 1fr);
	}

	@include tabletb {
		grid-template-columns: repeat(3, 1fr);
	}

	& .pop-dish:last-child {
		display: none;

		@include tabletb {
			display: flex;
		}
	}
}

.pop-dish {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 0 rem(16) rem(32) rem(16);
	border: rem(1) solid $border-input;
	border-radius: rem(56) rem(56) rem(32) rem(32);
	cursor: pointer;
	transition: background-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;

	&:hover {
		background-color: $white;
		border-color: $white;
		@include shd-h;
		.dish__title {
			h4 {
				color: $btn-hvr;
			}
		}
	}

	@include tabletb {
		border-radius: rem(88) rem(88) rem(42) rem(42);
		padding: 0 rem(32) rem(32) rem(32);
	}
}

.dish__img {
	position: relative;
	display: flex;
	justify-content: center;
	width: 100%;
	height: rem(40);

	.pic-wr {
		position: absolute;
		top: rem(-16);
		left: 50%;
		transform: translateX(-50%);
		width: rem(96);
		height: rem(96);
		border-radius: 50%;
		@include shd-h;

		@include laptop {
			width: rem(180);
			height: rem(180);
			top: rem(-32);
		}
	}

	@include laptop {
		height: rem(80);
	}
}

.dish__info {
	display: flex;
	flex-direction: column;
}

.dish__rating {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	margin-bottom: rem(10);

	.star,
	.likes {
		display: flex;
		flex-direction: column;
		align-items: center;

		.ic {
			width: rem(20);
			height: rem(20);
			background-repeat: no-repeat;
			background-size: contain;
			background-position: center;
		}

		p {
			@include adap-font(14, 18);
			font-weight: 600;
		}
	}

	.star {
		.ic {
			background-image: url("../img/icons/interface/ic-star.svg");
		}
	}

	.likes {
		.ic {
			transition: background-image 0.2s ease-in-out;
			background-image: url("../img/icons/interface/ic-heart.svg");
		}
	}

	@include laptop {
		margin-bottom: rem(32);
	}
}

.dish__desc {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.dish__title {
	margin-bottom: rem(6);
	text-align: center;
}

.dish__text {
	margin-bottom: rem(16);
	@include text-short(2);

	p {
		@include adap-font(14, 18);
		text-align: center;
	}
}

.dish__price {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;

	.price {
		margin-right: rem(16);

		p {
			@include adap-font(18, 28);
			font-weight: 600;
		}

		@include smart {
			margin-right: 0;
			margin-bottom: rem(8);
		}

		@include tabletb {
			margin-bottom: 0;
			margin-right: rem(8);
		}
	}

	@include smart {
		flex-direction: column;
	}

	@include tabletb {
		flex-direction: row;
	}
}

.dish__add-cart {
	border-color: $border-input;
}

.b2-btn {
	width: auto;
}

/* ---------- */

