// КНОПКА




/* База кнопки */

@mixin button($bg: $first, $borderColor: $second) {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	overflow: hidden;
	padding: em(16) em(32) em(17) em(32);
	box-shadow: none;
	font-family: Poppins, sans-serif;
	@include adap-font(14, 18);
	font-weight: 500;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	outline: none;
	white-space: nowrap;
	border-radius: em(4) em(20) em(4) em(20);
	border: $borderColor;
	color: $white;
	cursor: pointer;
	background: $bg;
	@content;



}

/* ---------- */




/* Основная кнопка */

.main-btn {
	position: relative;
	@include button($btn, transparent);
	transition: background-color 0.2s ease-in-out;

	.ic {
		position: relative;
		display: block;
		width: rem(40);
		height: rem(14);
		margin-left: rem(10);
		background-image: url('../img/icons/interface/ic-arrowTail.svg');
		background-repeat: no-repeat;
		background-size: contain;
		background-position: center;
	}

	&:hover {
		background-color: $btn-hvr;
	}

	&:active {
		background-color: $btn-act;
		box-shadow: none;
	}
}

/* ---------- */




/* Кнопка контур */

.border-btn {
	@include button(transparent, $btn);
	border-width: rem(1);
	border-style: solid;
	color: $btn;
	transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;

	&:hover {
		color: $white;
		background-color: $btn-hvr;
	}

	&:active {
		background-color: $btn-act;
		box-shadow: none;
	}
}

/* ---------- */




// КОНЕЦ КНОПКИ ==========

