// БЛОК «ARTICLES»

/* Основа */

.b4-tr {
	background-color: $bg-light;
}

.b4ptb {
	@include smart {
		padding-bottom: rem(60);
	}

	@include laptop {
		padding-bottom: rem(120);
	}
}

/* ---------- */

/* Основная статья */

.main-article {
	@include col-12;

	@include laptop {
		@include col-9;
		padding-right: rem(56);
	}
}

.main-article__title {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(16);

	.dish {
		margin-bottom: rem(10);

		@include smart {
			margin-bottom: 0;
			margin-right: rem(24);
		}

		@include laptop {
			margin-right: rem(48);
		}
	}

	@include smart {
		flex-direction: row;
		align-items: center;
	}

	@include laptop {
		margin-bottom: rem(32);
	}
}

.article-title {
	@include smart {
		max-width: rem(300);
	}

	@include laptop {
		max-width: rem(450);
	}
}

.articles__pic {
	margin-bottom: rem(4);
}

.main-article__info {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(16);

	@include smart {
		flex-direction: row;
	}

	@include laptop {
		justify-content: center;
	}
}

.main-article__desc {
	position: relative;
	z-index: 2;
	margin-bottom: rem(24);

	@include smart {
		width: 100%;

		@include col-7;
	}

	@include tabletm {
		margin-bottom: 0;
	}

	@include laptop {
		padding-right: rem(56);
	}
}

.main-article__text {
	margin-bottom: rem(16);
	@include text-short(4);

	p {
		@include adap-font(14, 18);
	}

	@include laptop {
		margin-bottom: rem(32);
	}
}

.main-article__btn {
	display: flex;
	justify-content: flex-start;

	.main-btn {
		width: auto;
	}
}

.main-article__pic {
	position: relative;
	height: rem(88);
	z-index: 1;

	.pic-wr {
		position: absolute;
		width: rem(160);
		right: 0;
		bottom: 0;

		@include smart {
			width: rem(220);
		}

		@include tabletm {
			width: rem(280);
		}

		@include tabletb {
			width: rem(330);
		}

		@include laptop {
			width: auto;
			top: 50%;
			transform: translateY(-50%);
			bottom: initial;
		}
	}

	@include smart {
		@include col-5;
		height: auto;
	}
}

/* ---------- */

/* Еще статьи */

.articles__block {
	display: flex;
	flex-direction: row;
	justify-content: center;
	@include col-12;

	& > * {
		margin-right: rem(8);
		&:last-child {
			margin-right: 0;
		}
		@include laptop {
			margin-right: 0;
			margin-bottom: rem(8);
			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	@include smart {
		flex-direction: row;
		align-items: center;
	}

	@include laptop {
		flex-direction: column;
		align-items: flex-end;
		@include col-3;
	}
}

.article__card {
	@include laptop {
		max-width: rem(303);
	}
}

.article__time {
	margin-bottom: rem(4);

	span {
		@include adap-font(12, 16);
		color: $text-light2;
	}
}

.article__title {
	@include text-short(2);

	p {
		@include adap-font(16, 20);
		font-weight: 600;
	}
}

.user {
	display: flex;
	flex-direction: row;
	align-items: center;

	.ic {
		width: rem(48);
		margin-right: rem(10);
		border-radius: 50%;
		border: 4px solid $white;
		@include shd-h;
	}
}

.user__info {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.user__name {
	display: flex;
	flex-direction: column;
	border-left: 1px solid $main-color;
	padding-left: rem(10);
	p {
		@include adap-font(14, 20);
		font-weight: 600;
	}
	span {
		@include adap-font(12, 16);
		color: $text-light2;
	}

	.stars {
		display: flex;
		flex-direction: row;
		align-items: center;
		img {
			width: rem(16);
		}
	}
}

/* ---------- */

// КОНЕЦ БЛОКА «ARTICLES» ==========
