// БЛОК «ПЕРВЫЙ ЭКРАН»

/* Основа */

.b-row {
	align-items: center;
}

/* ---------- */

/* Блок слева */

.banner__text {
	@include col-12;
	margin-bottom: rem(48);

	@include tabletb {
		@include col-8;
		margin-bottom: 0;
	}

	@include laptop {
		padding-right: rem(56);
	}
}

.title-tag,
.banner__title,
.banner__desc,
.banner__btns {
	margin-bottom: rem(16);
	@include smart {
		margin-bottom: rem(32);
	}
}

.title-tag {
	@include smart {
		width: auto;
	}
}

.banner__title {
	h1 {
		span {
			font-weight: inherit;
			color: $main-color;
			line-height: 120%;
		}
	}
}

.banner__desc {
	@include adap-font(14, 18);
	max-width: rem(510);
}

.banner__btns {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: flex-start;

	& > * {
		margin-top: rem(8);
		margin-bottom: rem(8);
	}

	@include smart {
		flex-direction: row;

		& > * {
			margin-top: 0;
			margin-bottom: 0;
			margin-right: rem(16);
			&:last-child {
				margin-right: 0;
			}
		}
	}
}

.bg-arr {
	position: relative;

	img {
		position: absolute;
		top: 0;
		left: 0;
		width: rem(245);
		height: rem(99);
	}
}

.banner__dishes {
	display: flex;
	flex-direction: column;

	@include smart {
		flex-direction: row;
		align-items: center;
	}

	& > * {
		margin-bottom: rem(8);
		&:last-child {
			margin-bottom: 0;
		}
		@include tabletm {
			margin-bottom: 0;
			margin-right: rem(8);
			&:last-child {
				margin-right: 0;
			}
		}
	}
}

.dish {
	display: flex;
	flex-direction: row;
	align-items: center;
	padding: rem(10);
	border: rem(1) solid $border-input;
	border-radius: rem(8);
	cursor: pointer;

	@include smart {
		flex-direction: column;
		width: rem(180);
		height: rem(152);
		border: none;
		background-image: url("../img/pic/dishes/dish-bg.svg");
		background-repeat: no-repeat;
		background-size: contain;
		background-position: left bottom;
		transition: background-image 0.2s ease-in-out;

		&:hover {
			background-image: url("../img/pic/dishes/dish-bgActive.svg");
		}
	}
}

.dish__pic {
	position: relative;

	.pic-wrap {
		// position: absolute;
		// top: 0;
		// left: 0;
		width: rem(56);
		height: rem(56);
		margin-right: rem(10);
		border-radius: 50%;
		@include shd-m;

		@include smart {
			width: rem(80);
			height: rem(80);
			margin-right: 0;
			margin-bottom: rem(20);
		}
	}
}

.dish__name {
	.name-wrap {
		p {
			@include adap-font(12, 16);
			font-weight: 600;
			color: $main-color;
			@include smart {
				text-align: center;
			}
		}
	}
}

/* ---------- */

/* Блок справа */

.banner__pic {
	display: flex;
	flex-direction: column;
	align-items: center;
	@include col-12;

	@include tabletb {
		display: flex;
		align-items: center;
		justify-content: flex-end;
		@include col-4;
	}
}

.banner__burger {
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
	background-image: url("../img/pic/food/burger-bg.svg");
	background-repeat: no-repeat;
	background-size: contain;
	background-position: center bottom;
}

.discount-wrap {
	position: absolute;
	top: rem(20);
	left: rem(20);
	z-index: 2;

	// @include laptop {
	// 	left: rem(-60);
	// }
}

.discount {
	position: relative;
	max-width: rem(154);
	border-radius: rem(16);
	padding: rem(16);
	@include shd-h;
	background-color: $white;

	&::after {
		position: absolute;
		top: rem(-12);
		right: rem(-12);
		content: "";
		width: rem(32);
		height: rem(32);
		background-image: url("../img/icons/interface/ic-sale.svg");
		background-repeat: no-repeat;
		background-size: contain;
		background-position: center;

		@include laptop {
			width: rem(56);
			height: rem(56);
			top: rem(-24);
			right: rem(-24);
		}
	}

	p {
		@include adap-font(20, 28);
		font-weight: 600;
		color: $main-color;
	}
	span {
		@include adap-font(16, 20);
		font-size: rem(20);
	}
}

.banner-slider {
	margin-bottom: rem(42);
}

.sldr-arr-btns-wrap {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
}

.banner-arr-btns {
	position: absolute;
	top: rem(-16);
}

.slider__arrow-btns {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	gap: rem(10);

	.ar-btn {
		display: flex;
		align-items: center;
		justify-content: center;
		width: rem(100);
		height: rem(40);
		border-radius: em(4) em(20) em(4) em(20);
		background-color: $white;
		@include shd-m;
		transition: background-color 0.2s ease-in-out;

		&:hover {
			cursor: pointer;
			background-color: $btn-hvr;

			.ar-btn-ic {
				svg {
					fill: $white;
				}
			}
		}

		&:active {
			box-shadow: none;
		}
	}
}

.ar-btn-ic {
	display: flex;
	align-items: center;
	justify-content: center;

	svg {
		width: rem(30);
		fill: $text-light3;
		transition: fill 0.2s ease-in-out;
	}
}

.ar-lft {
	transform: rotate(180deg);
}

/* ---------- */

// КОНЕЦ БЛОКА «ПЕРВЫЙ ЭКРАН» ==========
