// ПОДВАЛ




/* Основной блок */

.footer {
	display: flex;
	justify-content: center;
	flex-direction: column;
	border-top: 1px solid $border-input2;

}

.second-footer {
	border-top: 1px solid $border-input2;
}

/* ---------- */




/* Первая строчка футера */

.footer-row__first {
	display: flex;
	flex-direction: column-reverse;
	justify-content: center;
	padding-top: rem(24);
	padding-bottom: rem(24);
	color: $white;

	@include smart {
		flex-direction: row;
	}

	@include laptop {
		padding-top: rem(40);
		padding-bottom: rem(40);
	}
}

.footer__logo {
	display: flex;
	flex-direction: row;
	align-items: center;
	@include col-12;

	.logo-company {
		margin-right: rem(8);
		
		@include smart {
			margin-right: 0;
			margin-bottom: rem(24);
		}
	}

	@include smart {
		flex-direction: column;
		align-items: flex-start;
		@include col-4;
	}

}

.footer-desc {
	p {
		@include adap-font(12,16);
		color: $text-light3;
	}
	@include smart {
		max-width: rem(200);
	}
}

.footer__menu {
	margin-bottom: rem(24);

	@include smart {
		@include col-8;
	}
}


.footer__menu-wrap {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.menu-col {

	& ul > * {
		margin-bottom: rem(12);
		&:last-child {
			margin-bottom: 0
		}
	}

}

.footer__menu-title {
	@include adap-font(12,16);
	color: $text-light3;
}

.footer__menu-item {
	@include adap-font(11,18);
	color: $text;
	
}

/* ---------- */




/* Вторая строчка футера */

.footer-row__two {
	display: flex;
	justify-content: center;
	align-items: center;
	padding-top: rem(8);
	padding-bottom: rem(8);
	text-align: center;

	@include smart {
		flex-direction: row;
		justify-content: space-between;
	}

}

.footer__copyright {
	margin-top: rem(10);
	margin-bottom: rem(10);
	width: auto;

	p {
		color: $text-light3;
		@include adap-font(9, 16);
	}

	@include smart {
		display: flex;
		justify-content: flex-start;
	}

}

.footer__social {
	@include smart {
		display: flex;
		justify-content: flex-start;
		width: auto;
	}
}

/* ---------- */




