/* ---------- */




/* Секта без отступов */

.container {
	width: 100%;
	padding-left: rem(12);
	padding-right: rem(12);
	margin-left: auto;
	margin-right: auto;
}

.row {
	display: flex;
	flex-wrap: wrap;
}

.row > * {
	flex-shrink: 0;
	width: 100%;
	max-width: 100%;
}

