/* ---------- */




/* Breakpoints Mobile First */

@media (min-width: 576px) {
	.container {
		max-width: 540px;
	}
}

@media (min-width: 768px) {
	.container {
		max-width: 720px;
	}
}

@media (min-width: 992px) {
	.container {
		max-width: 960px;
	}
}

@media (min-width: 1200px) {
	.container {
		max-width: 1140px;
	}
}

@media (min-width: 1400px) {
	.container {
		max-width: 1200px;
	}
}

/* ---------- */




/* Отступы у блоков */

.paddTB__main-block {
	padding-top: rem(32);
	padding-bottom: rem(32);

	@include tabletm {
		padding-top: rem(64);
		padding-bottom: rem(64);
	}

	@include laptop {
		padding-top: rem(40);
		padding-bottom: rem(88);
	}


}

.paddTB {
	padding-top: rem(32);
	padding-bottom: rem(32);

	@include tabletm {
		padding-top: rem(64);
		padding-bottom: rem(64);
	}

	@include laptop {
		padding-top: rem(88);
		padding-bottom: rem(88);
	}

	@include desktop {
		padding-top: rem(124);
		padding-bottom: rem(124);
	}
}


/* ---------- */




/* Состояния */

.hide {
	display: none !important;
}

.show {
	display: flex !important;
}

.ov-h {
	overflow: hidden !important;
}

.cond-vis {
	visibility: visible !important;
}

.cond-opa {
	opacity: 1 !important;
}

/* ---------- */




/* Классы для быстрой проверки расположения блоков */

.br {
	border: 1px solid lightgreen;
}

.br-r {
	border: 1px solid lightcoral;
}

.br-bl {
	border: 1px solid lightskyblue;
}

.bg {
	background-color: rgba(144, 238, 144, 0.2);
}

