// МЕНЮ С ПРАВОЙ СТОРОНЫ ЭКРАНА



/* Основа */

.nav-offcanvas {
	position: fixed;
	z-index: 150 !important;
	width: 100%;
	height: 100vh;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	visibility: hidden;
	opacity: 0;
	background:rgba(15, 17, 21, 0.8);
	transition: opacity 0.3s ease-in-out, background 0.4s ease-out;
}

.nav-offcanvas__container {
	overflow-y: auto;
	position: relative;
	display: flex;
	flex-direction: column;
	margin-left: auto;
	justify-content: space-between;
	height: 100vh;
	max-width: 100%;
	width: 100%;
	border-top-left-radius: rem(32);
	border-bottom-left-radius: rem(32);
	opacity: 0;
	transform: translateX(100%);
	transition: transform 0.2s ease-in-out;
	background-color: $white;


	@include smart {
		max-width: rem(480);
	}

}

@keyframes anim-RightShiftNav {
	0% {
		opacity: 0;
		transform: translateX(100%);
	}
	100% {
		opacity: 1;
		transform: translateX(0);
	}
}

.anim-RightShiftNav {
	animation-name: anim-RightShiftNav;
	animation-duration: 0.3s;
	animation-delay: 0.1s;
	animation-iteration-count: 1;
	animation-timing-function: ease;
	animation-fill-mode: forwards;
}

