/* ---------- */




/* Пункты меню */

.nav-close-btn {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	padding: rem(8);
	cursor: pointer;
	// transition: all 0.3s ease-in-out;
	
	.close-ic {
		width: rem(10);
		height: rem(10);

		img {
			opacity: 0.5;
		}

	}

	p {
		font-size: rem(14);
		margin-left: rem(6);

	}

	&:hover {
		background-color: lighten($text, 82%);
		
		p {
			color: $btn;
		}
	}

	@include smart {
		padding: rem(16) rem(32);
		justify-content: flex-start;
	}


}

.nav-offcanvas__elements {

	span {
		display: block;
		text-align: center;
		@include adap-font(16, 20);
		padding-top: rem(12);
		padding-bottom: rem(12);
		font-weight: 700;
		text-decoration: none;
		border: none;
		transition: background 0.3s ease-in, color 0.3s ease-in;
		color: $text;
		cursor: pointer;
		
		@include smart {
			padding: rem(20) rem(32);
			text-align: left;
		}

		&:hover {
			background-color: lighten($main-color, 40%);
			color: $btn;
		}
	}
}

/* ---------- */




/* Контакты */

.nav-offcanvas__contacts {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: rem(24) rem(16);

	@include smart {
		align-items: flex-start;
		padding-left: rem(32);
		padding-right: rem(32);
	}

	& > * {
		margin-top: rem(8);
		margin-bottom: rem(8);
	}

	.phone-simp,
	.mail-simp,
	.address-simp {
		text-align: center;

		.ic {
			display: none;
		}

		@include smart {
			text-align: left;

			.ic {
				display: flex;
			}
		}
	}

	.address-simp {
		max-width: rem(320);
	}

}

/* ---------- */




