// ИНПУТЫ

/* База ИНПУТОВ */

@mixin input($bg: $first, $borderColor: $second) {
	position: relative;
	display: block;
	width: 100%;
	padding: em(16) em(16) em(16) em(48);
	border-radius: em(4) em(20) em(4) em(20);
	border: rem(1) solid $borderColor;
	font-family: 'Open Sans', Roboto, sans-serif;
	font-size: rem(16);
	appearance: none;
	cursor: text;
	outline: none;
	box-shadow: none;
	line-height: normal;
	background-color: $bg;
    transition: background-color .15s ease-in-out, border-color .15s ease-in-out;
	
	&::placeholder {
		color: $text;
	}

	@content;

}

/* ---------- */




/* Вид основных инпутов */

input[type=text],
input[type=email],
input[type=tel],
input[type=password] {
	@include input($input-bg, $border-input);
	position: relative;

	&:hover {
		border: rem(1) solid lighten($btn, 35%);
	}

	&:focus {
		border: rem(1) solid $btn;
	}

}

.input-box {
	position: relative;
	text-align: right;
	width: 100%;
}

