// ШАПКА




/* Основа */

// :root {
	// --header-height: rem(80);
// }

.header {
	width: 100%;
	transition: background 0.2s ease-in-out;
	border-bottom: 1px solid $border-input;
}

.header__container {
	padding-top: rem(8);
	padding-bottom: rem(8);

	@include smart {
		padding-top: rem(8);
		padding-bottom: rem(8);
	}

	@include tabletm {
		padding-top: rem(24);
		padding-bottom: rem(24);
	}

}

.header--fixed {
	position: fixed;
	z-index: 40;
	top: 0;
	left: 0;
	background-color: rgba(255,255,255, 0.95);
	@include shd-l;

	.header__container {
		@include tabletm {
			padding-top: rem(16);
			padding-bottom: rem(16);
		}
	}
}

.b-p {
	padding-top: rem(48);
	@include smart {
		padding-top: rem(57);
	}
	@include tabletm {
		padding-top: rem(74);
	}
	@include laptop {
		padding-top: rem(76);
	}
}

/* ---------- */




/* Логотип */

.logo-container {
	display: flex;
	align-items: center;
	@include col-1;

	@include laptop {
		@include col-1;
	}

	
}

.logo-company {

	a {
		
		&:before {
			display: none;
		}

	}

}

/* ---------- */




/* Меню */

.header__nav {
	display: none;

	ul {
		display:flex;
		flex-direction:row;

		li {
			list-style-type: none;

			a {
				display: block;
				font-size: rem(16);
				padding: rem(12) rem(16);
				color: $text;
				font-weight: 500;

				&:hover {
					color: $btn;
				}
			}
		}
	}

	@include laptop {
		display: flex;
		justify-content: center;
		@include col-9;
	}

}

/* ---------- */




/* Кнопки активностей */

.header__elements {
	@include col-11;
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	align-items: center;
	gap: rem(8);

	@include laptop {
		@include col-2;
	}

}

.header__search,
.header__cart,
.navbar-toggle {
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 50%;
	background-color: $white;
	width: rem(40);
	height: rem(40);
	cursor: pointer;
	transition: box-shadow 0.2s ease-in-out;

	.ic {
		display: flex;
		align-items: center;
		justify-content: center;
		width: rem(20);
		height: rem(20);

		svg {
			width: rem(20);
			height: rem(20);
			fill: $black;
			transition: fill 0.2s ease-in-out;
		}
	}

	&:hover {
		@include shd-btn;
		
		.ic {
			svg {
				fill: $btn;
			}
		}
	}
}


.header__call-btn {
	display: none;

	@include smart {
		display: flex;
		margin-right: rem(20);
		font-size: rem(14);
	}

	@include tabletb {
		font-size: rem(14);
	}

	@include laptop {
		display: none;
	}

}

/* ---------- */




/* Кнопка меню */

.navbar-toggle {
	border-radius: 50%;
	transition: background 0.2s ease-in-out, color 0.2s ease-in-out;

	&:hover {

		svg {
			opacity: 1;
			fill: $btn;
		}

	}

}

/* ---------- */


// КОНЕЦ ШАПКИ ==========



