/* ---------- */




/* Троеточие в конце строки */

@mixin text-short($numLines: 1) {
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
   
	@supports (-webkit-line-clamp: $numLines) {
	  overflow: hidden;
	  text-overflow: ellipsis;
	  white-space: initial;
	  display: -webkit-box;
	  -webkit-line-clamp: $numLines;
	  -webkit-box-orient: vertical;
	}
}

/* ---------- */




/* Фоновые картинки для ретины */

@mixin bg-x2($link) {
	@media
	only screen and (-webkit-min-device-pixel-ratio: 2),
	only screen and (   min--moz-device-pixel-ratio: 2),
	only screen and (     -o-min-device-pixel-ratio: 2/1),
	only screen and (        min-device-pixel-ratio: 2),
	only screen and (                min-resolution: 192dpi),
	only screen and (                min-resolution: 2dppx) { 
		background-image: url($link);
	}
}

/* ---------- */




/* Анимации появления текста */

@mixin text-anim() {
	transform: translate(0px, 20%);
	opacity: 0;
	transition: all 0.8s ease .3s;
}

@mixin text-anim-show() {
	transform: translate(0px, 0);
	opacity: 1;
}

/* ---------- */




/* Вертикальное выравнивание */

@mixin v-center {
	top: 50%;
	-ms-transform: translateY(-50%);
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
 }

/* ---------- */




/* Скрыть но оставить */

@mixin visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;
}

/* ---------- */




/* Тень */

@mixin shd-l {
	box-shadow: 0px 0.25rem 0.375rem rgba(0, 0, 0, 0.06);
}

@mixin shd-m {
	box-shadow: 0px 0.9375rem 1.25rem rgba(0, 0, 0, 0.05);
}

@mixin shd-h {
	box-shadow: 0px 0.625rem 1.875rem rgba(43, 43, 43, 0.2);
}

@mixin shd-btn {
	box-shadow: 0px em(10) em(30) rgba(249, 85, 85, 0.2);
}

